export type TLang = Record<string, Record<string, string> & { en: string; fr: string }>;

declare global {
  interface Window {
    lang: string;
    // i18n: (file: TLang, key: string & keyof TLang) => string;
    // i18n: <T = TLang, _Key = Extract<keyof TLang, 'string'>>(file: TLang, key: _Key) => TLang[_Key][string];
    i18n: <T = TLang, _Key extends keyof TLang = Extract<keyof TLang, 'string'>>(
      file: TLang,
      key: _Key,
      params?: Record<number, string>
    ) => TLang[_Key][string];
  }
}

const navLang = window?.navigator?.language?.toLowerCase() || '';
window.lang = ['en', 'en-us'].includes(navLang) ? 'en' : 'fr';
window.i18n = (file, key, params) => {
  let value = file?.[key][window.lang];
  if (params && Object.keys(params).length > 0) {
    for (let i = 0; i < Object.keys(params).length; i++) {
      value = value.replace(`{${i}}`, params[i]);
    }
  }
  return value;
};
