import { TImg } from '@miq/componentjs';
import { TextChoicesType } from '@miq/formjs';
import { WithMetaFields } from '@miq/utiljs';

export type TPriceData = { currency: string; amount: string; amountWithSymbol: string };
export type TPrice = number | TPriceData;
const m = [
  'slug',
  'name_truncated',
  'category_data',
  'cover_data',
  'sizes',
  'retail_price_data',
  'sale_price_data',
  'size_count',
  'supplier_item',
  'stage',
  'dt_published',
  'is_published',
  'meta_slug',
  'name',
  'description',
  'category',
  'cover',
  'retail_price',
  'is_on_sale',
  'sale_price',
  'is_pre_sale',
  'is_pre_sale_text',
  'is_oos',
  'position',
  'is_pinned',
  'is_explicit',
];

type TObj = Record<string, any>;
export type TProduct = WithMetaFields<{
  readonly slug?: string;

  category?: string;
  category_data?: TCategory;
  name?: string;

  cover?: string;
  cover_data?: TImg;
  images?: string[];
  images_data?: TImg[];

  readonly name_truncated?: string;
  description?: string;
  retail_price?: TPrice;
  retail_price_data?: TPriceData;
  is_on_sale?: boolean;
  sale_price?: TPrice;
  sale_price_data?: TPriceData;
  is_pre_sale?: boolean;
  is_pre_sale_text?: string;
  is_pre_sale_dt?: string | Date;
  url?: string;

  has_attributes?: boolean;
  attributes?: TProductAttribute[];
  sizes?: TProductSize[];
  size_count?: number;

  is_pinned?: boolean;
  is_explicit?: boolean;
  is_published?: boolean;
  dt_published?: string | Date;

  supplier_item?: SupplierItemType;
  position?: number;
  color_group_pk?: string;
  stage?: string;
  is_oos?: boolean | undefined;

  readonly detail_path?: string;

  readonly created?: string | Date;
  readonly updated?: string | Date;
}>;

export type CategoryChoicesType = {
  items: TextChoicesType;
};

export type TProductStageData = { name: string; label: string; value: string };

export type TCategory = WithMetaFields<
  | TObj
  | {
      slug?: string;
      supplier_item_id?: string;
      name?: string;
      description?: string;
      cover?: TImg;
      position?: number;

      is_published?: boolean;
      dt_published?: string | Date;

      cover_data?: TImg;
      products_count?: number;
      published_count?: number;
      draft_count?: number;
      readonly detail_path?: string;

      created?: string | Date;
      updated?: string | Date;
    }
>;

export type SupplierItemType = {
  slug?: string;
  item_id?: string;
  item_sn?: string;
  category?: string;
  url?: string;
  cost?: number;
  created?: string;
  updated?: string;
};

export enum SupplierName {
  SHEIN = 'SHEIN',
  PLT = 'PLT',
  FNOVA = 'FNOVA',
}

export type TSupplierOrder =
  | TObj
  | {
      slug?: string;
      name?: string;
      supplier?: string;
      order_id?: string;
      products?: TProduct[];
      items?: any[];
      items_cost?: number;
      items_revenue?: number;
      avg_cost?: number;
      by_categories?: { category__name: string; count: number }[] | undefined;

      currency?: string;
      is_paid?: boolean;
      is_paid_dt?: string;
      is_fulfilled_dt?: string;
      total_cost?: number;
      total_cost_data?: TPriceData;
    };

export type TProductSize =
  | TObj
  | {
      slug?: string;
      name?: string;
      code?: string;
      quantity?: number;
    };

export type TProductAttribute =
  | TObj
  | {
      slug?: string;
      name?: string;
      value?: string;
    };

//==============================================================
//                SETTING
//==============================================================

export type TCurrency = {
  name: string;
  label: string;
  value: string;
};

export type TStoreSettings = {
  slug?: string;
  config: { has_suppliers: boolean; show_active_suppliers: boolean; show_product_supplier: boolean };
  currency?: 'XOF' | 'USD' | string;
  currencies?: TCurrency[];
  returns?: string;
  returns_html?: string;
  size_guide?: string;
  size_guide_html?: string;
};
